.navigation {
    padding: 20px;
    width: 250px;
    display: flex;
    flex-direction: column;
    gap: 20px;

    background-color: var(--white);
    box-shadow: 4px 0 16px rgba(0, 0, 0, 0.08);

    &__header {
        display: flex;
        flex-direction: column;

        & h1 {
            font-weight: 700;
            font-size: 14px;
            line-height: 24px;
            letter-spacing: 0.3px;
        }

        & span {
            font-size: 11px;
            line-height: 16px;
        }

        &::after {
            content: '';
            display: block;
            width: 100%;
            height: 1px;
            background-color: var(--light-grey);
            margin-top: 20px;
        }
    }

    &__body {
        display: flex;
        flex-direction: column;
        gap: 12px;
    }

    &__footer {
        margin-top: auto;
        display: flex;

        &-copyright {
            margin: 0 auto;
            font-size: 11px;
            line-height: 16px;
            color: var(--black--opacity-03);
        }
    }

    @media (max-width: 768px) {
        position: fixed;
        z-index: 2;
        bottom: 0;
        width: 100%;
        padding: 12px 20px;
        flex-direction: row;
        justify-content: space-between;

        &__header {
            min-width: fit-content;

            &::after {
                content: none;
            }
        }

        &__body {
            flex-direction: row;

            & button {
                padding: 8px;

                & svg {
                    position: static;
                    width: 20px;
                    height: 20px;
                }

                & span {
                    display: none;
                }
            }
        }

        &__footer {
            display: none;
        }
    }
}
